.body-aviso-legal {
  background: url(../images/background-legal-warning.png) no-repeat;
  background-position: top;
  background-size: cover;
  overflow-y: hidden;
  .contenedor-aviso-legal {
    height: 100%;
    margin-top: 80px;
    margin-bottom: 60px;
    padding: 2rem;
    background: #222a31;
    border-radius: 30px 30px 0 0;
    color: #fff;
    overflow-y: auto;
    position: absolute;
    padding-bottom: 160px;
    .sub-title {
      font-weight: 800;
    }
    p {
      font-weight: 300;
    }
  }
}